.new-dapp-label{border-radius:2px;background:#ff445b;-webkit-animation:pulse 3s infinite;animation:pulse 3s infinite}@-webkit-keyframes pulse{0%{-webkit-box-shadow:0 0 0 0 rgba(255,255,255,.4)}70%{-webkit-box-shadow:0 0 0 5px rgba(255,255,255,0)}100%{-webkit-box-shadow:0 0 0 0 rgba(255,255,255,0)}}@keyframes pulse{0%{-webkit-box-shadow:0 0 0 0 rgba(255,255,255,.4);box-shadow:0 0 0 0 rgba(255,255,255,.4)}70%{-webkit-box-shadow:0 0 0 5px rgba(255,255,255,0);box-shadow:0 0 0 5px rgba(255,255,255,0)}100%{-webkit-box-shadow:0 0 0 0 rgba(204,169,44,0);box-shadow:0 0 0 0 rgba(204,169,44,0)}}.v-system-bar .v-icon{font-size:36px !important;color:#fff !important}.wallet-sidemenu .v-list-item--link{border-top:none}.wallet-sidemenu .v-list-item--active .v-list-item__content .v-list-item__title{font-weight:500 !important}.wallet-sidemenu .v-list-group__header__append-icon .v-icon{color:var(--v-white-base) !important}.wallet-sidemenu .v-divider{border-color:rgba(255,255,255,.22) !important}.wallet-sidemenu .v-list-item--link:hover{background-color:rgba(255,255,255,.2) !important}.wallet-sidemenu .v-list-item:after{min-height:40px !important}.wallet-sidemenu .mew-body.font-weight-bold{font-weight:400 !important}.wallet-sidemenu .v-list-item--active.v-list-item:not(.v-list-group__header){background-color:rgba(255,255,255,.1) !important}.wallet-sidemenu .v-list-item--active::before{opacity:0 !important}.wallet-sidemenu .v-navigation-drawer__content{margin-right:5px;margin-bottom:10px}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar{width:4px;height:4px}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-button{width:0;height:0}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-thumb{background:#7b91ac;border:0 none #fff;border-radius:50px}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-thumb:hover{background:#7b91ac}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-thumb:active{background:#4b4949}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-track{background:#e1dfdf;border:0 none #fff;border-radius:39px}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-track:hover{background:#ddd5d5}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-track:active{background:#dedede}.wallet-sidemenu .v-navigation-drawer__content::-webkit-scrollbar-corner{background:transparent}.wallet-sidemenu .matomo-tracking-switch .v-label{color:var(--v-white-base)}